<template >
  <div class="vx-col w-full mb-base" >

    <vs-popup class="holamundo" title="edit user" :active.sync="popupActive">
      <div class="w-full mt-3">
          <label>{{$t("first_name")}}</label>
          <vs-input v-validate="'required'" data-vv-validate-on="blur" name="firstname" type="text" placeholder="first name"
            v-model="user.first_name" class="w-full" />
          <span class="text-danger text-sm">{{ errors.first('firstname') }}</span>
        </div>
        <div class="w-full mt-3">
          <label>{{$t("last_name")}}</label>
          <vs-input v-validate="'required'" data-vv-validate-on="blur" name="lastname" type="text" placeholder="last name"
            v-model="user.last_name" class="w-full" />
          <span class="text-danger text-sm">{{ errors.first('lastname') }}</span>
        </div>
        <div class="vx-row">

          <div class="vx-col md:w-1/2">
            <div class="mt-3">
              <label>date of birth</label>
              <datepicker placeholder="Select Date" v-model="user.date_of_birth"></datepicker>
            </div>

          </div>
          <div class="vx-col md:w-1/2 mt-3">
            <label>{{$t('tel')}}</label>
            <vs-input ref="tel" type="tel" data-vv-validate-on="tel" name="tel" placeholder="tel" v-model="user.tel"
              class="w-full" />


          </div>
        </div>
        <div class="w-full mt-3">
          <label>{{$t("address")}}</label>
          <vs-input ref="address" type="text" data-vv-validate-on="blur" name="address" placeholder="address" v-model="user.address"
            class="w-full" />
        </div>
        <div class="w-full mt-3">
          <label>{{$t("status")}}</label>
          <vs-select class="w-full" v-model="user.flag">
          <vs-select-item :key="index" :value="item.flag_id" :text="item.en_name" v-for="(item,index) in status" />
        </vs-select>
        </div>
        <div class="w-full mt-3">
          <label>{{$t("username")}}</label>
          <vs-input v-validate="'required|min:3'" data-vv-validate-on="blur" name="username" placeholder="Username"
            v-model="user.username" class="w-full" disabled />
          <span class="text-danger text-sm">{{ errors.first('username') }}</span>
        </div>

        <div class="w-full mt-3">
          <label>{{$t('password')}}</label>
          <vs-input ref="password" type="password" data-vv-validate-on="blur" v-validate="'required|min:6|max:10'"
            name="password" placeholder="Password" v-model="user.password" class="w-full mt-3" />
          <span class="text-danger text-sm">{{ errors.first('password') }}</span>
        </div>



        <div class="w-full mt-3">
          <label>{{$t("confirm_password")}}</label>
          <vs-input type="password" v-validate="'min:6|max:10|confirmed:password'" data-vv-validate-on="blur"
            data-vv-as="password" name="confirm_password" placeholder="Confirm Password" v-model="user.confirm_password"
            class="w-full" />
          <span class="text-danger text-sm">{{ errors.first('confirm_password') }}</span>
        </div>
        <vs-button color="success" class="mt-5 mr-4" type="border" :disabled="!validateForm" @click="save()">save</vs-button>
        <vs-button color="danger" class="mt-5" type="border" @click="popupActive=false">close</vs-button>
        </vs-popup>

    <vx-card v-if="!view_file">
        <vs-table pagination max-items="10" search :data="users">

                    <template slot="thead">
                      <vs-th sort-key="username">{{$t('username')}}</vs-th>
                      <vs-th sort-key="first_name">{{$t('first_name')}}</vs-th>
                      <vs-th sort-key="last_name">{{$t('last_name')}}</vs-th>

                      <vs-th sort-key="operating_unit_name">{{$t('dob')}}</vs-th>
                      <vs-th sort-key="operating_unit_name">{{$t('address')}}</vs-th>
                      <vs-th sort-key="operating_unit_name">{{$t('tel')}}</vs-th>
                      <vs-th sort-key="operating_unit_name">{{$t('status')}}</vs-th>

                      <vs-th>{{$t('approve')}}</vs-th>
                       <vs-th>{{$t('view_file')}}</vs-th>
                      <vs-th>{{$t("edit")}}</vs-th>
                      <vs-th>{{$t("delete")}}</vs-th>
                    </template>

                    <template slot-scope="{data}">
                      <vs-tr :data="tr" :key="index" v-for="(tr, index) in data">

                        <vs-td :data="tr.username">
                          {{tr.username}}
                        </vs-td>

                        <vs-td :data="tr.first_name">
                          {{ tr.first_name }}
                        </vs-td>

                        <vs-td :data="tr.last_name">
                          {{ tr.last_name }}
                        </vs-td>

                        <vs-td :data="tr.date_of_birth">
                          {{ tr.date_of_birth }}
                        </vs-td>
                        <vs-td :data="tr.address">
                          {{ tr.address }}
                        </vs-td>
                        <vs-td :data="tr.tel">
                          {{ tr.tel }}
                        </vs-td>
                        <vs-td :data="tr.en_name">
                          {{ tr.en_name }}
                        </vs-td>

                         <vs-td :data="tr.flag">
                            <vs-button color="success" type="border" v-if="tr.flag=='1'" @click="approve(tr)">approve</vs-button>
                        </vs-td>
                        <vs-td :data="tr.username">
                           <feather-icon icon="FileTextIcon" class="cursor-pointer w-6 h-6" size="small" @click="view(tr)"></feather-icon>
                        </vs-td>
                        <vs-td>
                          <feather-icon icon="EditIcon" class="cursor-pointer w-6 h-6" size="small" @click="edit(tr)"></feather-icon>
                        </vs-td>
                        <vs-td>
                          <feather-icon icon="Trash2Icon" class="cursor-pointer w-6 h-6" size="small" @click="delete_user(tr)"></feather-icon>
                        </vs-td>
                      </vs-tr>
                    </template>
                  </vs-table>

    </vx-card>
    <vx-card v-if="view_file">
      <vs-table pagination max-items="10" search :data="files">

                    <template slot="thead">
                      <vs-th sort-key="username">{{$t('username')}}</vs-th>
                      <vs-th sort-key="file_name">{{$t('file_name')}}</vs-th>
                      <vs-th sort-key="upload_date">{{$t('upload_date')}}</vs-th>


                      <vs-th class="text-center">{{$t("download")}}</vs-th>
                      <vs-th>{{$t("delete")}}</vs-th>
                    </template>

                    <template slot-scope="{data}">
                      <vs-tr :data="tr" :key="index" v-for="(tr, index) in data">

                        <vs-td :data="tr.username" style="min-width:150px">
                          {{tr.username}}
                        </vs-td>

                        <vs-td :data="tr.file_name">
                          {{ tr.file_name }}
                        </vs-td>

                        <vs-td :data="tr.upload_date">
                          {{ tr.upload_date }}
                        </vs-td>


                        <vs-td class="text-center">
                          <feather-icon icon="DownloadIcon" class="cursor-pointer w-6 h-6" size="small" @click="download(tr)"></feather-icon>
                        </vs-td>
                        <vs-td>
                          <feather-icon icon="Trash2Icon" class="cursor-pointer w-6 h-6" size="small" @click="delete_file(tr)"></feather-icon>
                        </vs-td>
                      </vs-tr>
                    </template>
                  </vs-table>
             <div class="mt-5">
               <vs-button color="warning"  @click="view_file=false">back</vs-button>
               <br><br>
             </div>
    </vx-card>
  </div>
  <!-- <button>click</button> -->
</template>
<style lang="scss" scoped>

  .require{
    color: red;
  }

  .danger input {
    padding: .7rem;
    font-size: 1rem;
    border-radius: 5px;
    border: 1px solid red;
    color: #626262;
    width: 100%;
  }
   .error{
    color:red;
     font-size:80%;
  }
</style>


<script>
import Datepicker from 'vuejs-datepicker';
import service from '@/service/service';

import moment from 'moment';
// import firebase from 'firebase/app'à¸§
// import * as firebase from 'firebase/app';

// import '@firebase/messaging';
export default {
  components:{
    Datepicker
  },
   data(){
      return {
        view_file:false,
         'tableList': [
        'vs-th: Component',
        'vs-tr: Component',
        'vs-td: Component',
        'thread: Slot',
        'tbody: Slot',
        'header: Slot',
      ],
      popupActive:false,
      renderComponent:true,
      users: [

      ],
      status:[

      ],
      files:[],
      user:{}

      }
   },

   computed:{
      validateForm() {
            return !this.errors.any() && this.user.username != ''&&this.user.first_name!='' && this.user.last_name != '' && this.user.password != '' && this.user.confirm_password != '';
        }
   },
   created(){
      service.getData("/get_status").then((result)=>{
        this.status=result;
      })
      this.get_user();


   },
   methods: {
     get_user(){
       service.getData('/get_usernotapprove').then((result)=>{
        this.users=result.map((item)=>{
          item.date_of_birth=item.date_of_birth==""?"":moment(item.date_of_birth).format("DD/MM/YYYY");
          return item;
        });
      })
     },
      delete_user(user){
        this.$swal({
          title: 'Are you sure to delete this user?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'delete'
        }).then((result) => {
          console.log(result);
          if (result.value) {
           service.postData("delete_user",{username:user.username}).then((result)=>{

              this.$swal(
              'successfull!',
              'user has been deleted.',
              'success'
            ).then((result)=>{
              this.get_user();
            })
           })
          }
      })
      },
      edit(user){
        this.user=user;
        this.user.password="";
        this.user.password="";
        this.popupActive=true
      },
      save(){
        if(this.validateForm){
          service.postData("edit_user",this.user).then((result)=>{
            this.popupActive=false;
            this.$swal(
              'successfull!',
              'user has been updated.',
              'success'
            ).then((result)=>{
              this.get_user();
            })
          })
        }
      },
      approve(user){
        this.$swal({
          title: 'Are you sure to approvice this user?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'apprivce'
        }).then((result) => {
          console.log(result);
          if (result.value) {
           service.postData("approve",{username:user.username}).then((result)=>{
              this.$swal(
              'successfull!',
              'user has been approved.',
              'success'
            ).then((result)=>{
              this.get_user();
            })
           })
          }
      })
    },
    download(file){
      var win = window.open("http://localhost:3000/files/"+file.file_name, '_blank');
      win.focus();
    },
    delete_file(file){
      this.$swal({
          title: 'Are you sure to delete this file?',
          text: "You won't be able to revert this!",
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'delete'
        }).then((result) => {
          console.log(result);
          if (result.value) {
            service.postData("delete_file",{file_id:file.file_id}).then((result)=>{
                this.$swal('file was deleted','','success').then((result)=>{
                  this.forceRerender();
                });
              })
          }
      })

    },
    view(user){
      console.log(user)
     if(user.file_name!=""&&user.file_name!=null){
        var win = window.open("http://localhost:3000/files/"+user.file_name, '_blank');
        win.focus();
     }else{
       this.$swal('file does not exits','','warning')
     }
      // this.view_file=true;
      // service.postData('get_file',{username:user.username}).then((result)=>{
      //   this.files=result;
      // })
    }
   },
  watch: {

    }
}
</script>

